.tr-home-section-5-wrap {
  padding: 100px 0px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  // z-index: -1;
  // background-color: #000;
}
.tr-home-section-5-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  color: #000;
  .tr-home-section-5-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    .tr-home-section-5-heading {
      display: flex;
      font-size: 52px;
      margin-bottom: 20px;
      padding-left: 230px;
      span {
        font-weight: 600;
        color: #ba9e66;
      }
    }
  }
}
.tr-home-section-5-smallh {
  font-size: 52px;
}
.tr-home-section-5-list {
  display: flex;
  flex-direction: column;
  font-size: 28px;
  line-height: 1.5;
  li {
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 14px;
  }
}
.tr-home-section-5-image-container {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 600px;
  height: 470px;
  top: 0%;
  left: 0%;
  background-image: url(../../../../assets/digital-3d-imaging.jpg);
}
@media (max-width: 1366px) {
  .tr-home-section-5-wrap {
    // padding: 100px 30px !important;
  }
  .tr-home-section-5-image-container {
    img {
      width: 600px !important;
      height: 500px !important;
    }
  }
  .tr-home-section-5-container {
    .tr-home-section-5-header {
      .tr-home-section-5-heading {
        font-size: 45px !important;
        padding-left: 210px !important;
      }
    }
  }
  .tr-home-section-5-smallh {
    font-size: 45px;
  }
  .tr-home-section-5-list {
    font-size: 26px;
    li {
      gap: 14px !important;
      svg {
        height: 18px !important;
        width: 18px !important;
      }
    }
  }
}
