.home-section-2 {
  // background-color: #000;
  background-color: #fff;
  color: #000;
  // color: #fff;
}
.home-section-2-wrapper {
  padding: 100px 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0px;
}
.home-section-2-description {
  font-size: 28px;
  line-height: 1.5;
  display: flex;
  // padding: 0px 120px;
  padding-left: 60px;
  padding-right: 60px;
  border-right: 1px solid #fff;
  // padding-right: 40px;
  p {
    text-align: center;
  }
}
.home-section-2-adv {
  font-size: 28px;
  display: flex;
  // align-items: center;
  flex-direction: column;
  position: relative;
  // background-color: red;
  // height: 28px * 4;
  gap: 20px;
  align-items: center;
  .adv-free {
    // height: 28px * 1.5;
    padding: 0 20px;
    font-weight: 700;

    // font-size: 3px;
  }
  .adv-description {
    // display: flex;
    // flex-direction: column;
    // padding: 0px 20px;
    // border-bottom: 1px solid #fff;
    // gap: 40px;
    // height: 28px * 1.5;
    overflow: hidden;

    // text-align: center;
    .adv-description-inner {
      display: flex;
      gap: 40px;
      // animation: rotate 6s ease-in-out infinite;
      .hover-underline-animation {
        margin-bottom: 4px;
        // padding: 20px;
        display: block;
        font-weight: 700;
        color: #ba9366;
      }
    }
  }
}
.hover-underline-animation {
  display: inline-block;
  position: relative;
}

@media (max-width: 1366px) {
  .home-section-2-wrapper {
    gap: 24px;
  }
  .home-section-2-description {
    font-size: 26px;
  }
  .home-section-2-adv {
    font-size: 26px;
  }
}
@media (max-width: 1200px) {
  .home-section-2-wrapper {
    gap: 24px;
  }
  .home-section-2-description {
    font-size: 24px;
  }
  .home-section-2-adv {
    font-size: 24px;
  }
}
@media (max-width: 944px) {
  .home-section-2-wrapper {
    gap: 24px;
  }
  .home-section-2-description {
    font-size: 22px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .home-section-2-adv {
    font-size: 22px;
  }
}

@media (max-width: 704px) {
  .home-section-2-wrapper {
    padding: 50px 30px 30px;
  }
  .home-section-2-description {
    font-size: 20px;
    // padding: 8px 20px;
  }
  .home-section-2-adv {
    font-size: 20px;
  }
}
@media (max-width: 560px) {
  .home-section-2-wrapper {
    flex-direction: column !important;
    gap: 12px;
  }
  .home-section-2-description {
    font-size: 18px;
    padding: 0px;
    border: none !important;
    // border-bottom: 1px solid #fff !important;
  }
  .home-section-2-adv {
    flex-direction: column !important;
    height: auto !important;
    font-size: 18px;
    // padding: 20px 80px;
    gap: 0 !important;
    .adv-free {
      padding: 10px !important;
      border: none !important;
    }
  }
}
@media (max-width: 320px) {
  .home-section-2-wrapper {
    padding: 30px 40px;
    // padding-bottom: 50px;
    // padding-bottom: 0;
    flex-direction: column !important;
    gap: 12px;
  }
  .home-section-2-description {
    font-size: 16px;
    padding: 0px;
    border: none !important;
    // border-bottom: 1px solid #fff !important;
  }
  .home-section-2-adv {
    flex-direction: column !important;
    height: auto !important;
    font-size: 16px;
    // padding: 20px 80px;
    gap: 0 !important;
    .adv-free {
      padding: 10px !important;
      border: none !important;
    }
  }
}
