.home-section-1 {
  padding: 100px 20px;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  color: #000;
}
.home-container {
}
.home-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 80px;
  .home-header-slogan {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 16px;
    .home-header-name {
      font-size: 52px;
      padding-right: 30px;
      border-right: 4px solid #000;
    }
    .tr {
      border-right: none;
    }
    .home-header-name-2 {
      font-size: 45px;
      letter-spacing: -0.5px;
    }
  }
  .home-header-small {
    font-size: 28px;
    font-weight: 400;
  }
}
.home-button {
  cursor: pointer;
  font-size: 28px;
  transition: all 1s;
  transform-origin: top;
  background: #ba9e66;
  position: relative;
  z-index: 1;
  color: #fff;
  padding: 20px;
  font-weight: 600;
  svg {
    display: none;
  }
}
.home-button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0);
  transform-origin: center bottom;
  background: #000;
  z-index: -1;
  transition: transform 0.3s;
}

.home-button:hover::after {
  transform: scaleY(1);
}

