.tr-home-section-0 {
  // background-color: #000;
  background-color: #fff;
  color: #000;
  // color: #fff;
}
.tr-home-section-0-wrapper {
  padding: 100px 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0px;
}
.tr-home-section-0-description {
  font-size: 28px;
  line-height: 1.5;
  display: flex;
  padding: 0px 120px;
  border-right: 1px solid #fff;
  // padding-right: 40px;
  p {
    text-align: center;
  }
}
.tr-home-section-0-adv {
  font-size: 28px;
  display: flex;
  // align-items: center;
  flex-direction: column;
  position: relative;
  // background-color: red;
  // height: 28px * 4;
  gap: 20px;
  align-items: center;
  .tr-adv-free {
    // height: 28px * 1.5;
    padding: 0 20px;
    font-weight: 700;

    // font-size: 3px;
  }
  .tr-adv-description {
    // display: flex;
    // flex-direction: column;
    // padding: 0px 20px;
    // border-bottom: 1px solid #fff;
    // gap: 40px;
    // height: 28px * 1.5;
    overflow: hidden;

    // text-align: center;
    .tr-adv-description-inner {
      display: flex;
      gap: 40px;
      // animation: rotate 6s ease-in-out infinite;
      .tr-hover-underline-animation {
        margin-bottom: 4px;
        // padding: 20px;
        display: block;
        font-weight: 700;
        color: #ba9366;
      }
    }
  }
}
@media (max-width: 1366px) {
  .tr-home-section-0-description {
    font-size: 26px;
  }
}
@media (max-width: 1200px) {
  .tr-home-section-0-description {
    font-size: 24px;
    padding-left: 40px;
    padding-right: 40px;
  }
  .tr-home-section-0-wrapper {
  }
}
@media (max-width: 944px) {
  .tr-home-section-0-description {
    font-size: 22px;
  }
}
@media (max-width: 704px) {
  .tr-home-section-0-wrapper {
    // padding-top: 50px;
    padding: 50px 30px 30px;

    // padding-bottom: 30px;
  }
  .tr-home-section-0-description {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 20px;
  }
}
@media (max-width: 560px) {
  .tr-home-section-0-description {
    font-size: 18px;
  }
}
@media (max-width: 320px) {
  .tr-home-section-0-description {
    font-size: 16px;
  }
}
