.tr-footer {
}
.tr-footer-container {
  background-color: #000;
  display: grid;
  grid-template-columns: 1fr;
  padding: 60px 80px;
  align-items: baseline;
  gap: 60px;
  justify-content: center;
}
.tr-footer-wrap {
  display: flex;
  color: #fff;
  padding: 16px 40px 40px;
  flex-direction: column;
}

.tr-footer-heading {
  font-size: 24px;
  text-align: center;
}
.tr-footer-description-1 {
  text-align: center;
  font-size: 20px;
  line-height: 1.7;
  letter-spacing: -0.5px;
  font-weight: 400;
  color: #ffffff81;
}
.tr-footer-description-2 {
  display: flex;
  flex-direction: column;
  gap: 20px;
  font-weight: 500;
  letter-spacing: -0.5px;
  font-size: 20px;
  a {
    color: #fff;
    transition: all 0.5s;
  }

  a:hover {
    color: #ba9e66;
  }
  div {
    cursor: pointer;
    transition: all 0.5s;
  }
}
.tr-footer-description-2 div:hover {
  color: #ba9e66;
}
.tr-footer-description-2:last-child {
  color: #ba9e66;
}
.tr-footer-description-3 {
  text-align: center;
  line-height: 1.7;
  font-size: 16px;
  color: #ffffff81;
}
.tr-footer-desc {
  display: flex;
  // flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: space-around;
}
.tr-footer-desc-info {
  h4 {
    font-size: 18px;
  }
  div {
    color: #ffffff81;
  }
}
.tr-footer-icons {
  margin-top: 30px;
  margin-right: 116px;
}

.tr-copyrights {
  margin-top: 40px;
  font-size: 18px;
  color: #000;
  text-align: center;
}
.tr-footer-foot {
  background-color: #fff;
  padding: 40px 80px;
}
.tr-footer-foot-container {
  display: flex;
  margin-bottom: 30px;
  align-items: center;
  justify-content: space-evenly;
}
.tr-footer-by {
  font-size: 24px;
  font-weight: 700;
}
@media (max-width: 1200px) {
  .tr-footer-container {
    padding: 20px 20px !important;
    gap: 10px !important;
  }
  .tr-footer-wrap {
    padding: 40px 20px;
  }
  .tr-footer-heading {
    font-size: 22px !important;
  }
  .tr-footer-description-1 {
    font-size: 18px !important;
  }
  .tr-footer-description-2 {
    font-size: 18px !important;
  }
  .tr-footer-description-3 {
    font-size: 14px;
  }
  .tr-footer-desc-info {
    h4 {
      font-size: 16px !important;
    }
  }
  .tr-copyrights {
    font-size: 16px !important;
  }
  .tr-footer-by {
    font-size: 22px !important;
  }
  .tr-footer-foot-container {
    div {
      img {
        width: 60px !important;
        height: 46px !important;
      }
    }
    .tr-iimm {
      img {
        width: 200px !important;
        height: 41px !important;
      }
    }
  }
}
